.card {
  margin: 1em 0;
}

.card-content > :not(:first-of-type) {
  margin: 1em 0;
}

.card-content img {
  float: left;
  max-width: 50%;
}

.margined-horizontal {
  margin-left: 0.3em;
  margin-right: 1em;
}

.margined-bottom {
  margin-bottom: 1em;
}

.horizontal {
  display: flex;
  flex-direction: row;
  align-items: center;
}